body {
  background-color: #d4a17f; 
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.GameContainer {
  padding: 60px 60px 60px 60px;
}

.circle {
  height: 25px;
  width: 25px;
  border-radius: 50%;
  display: inline-block;
}

.smallCircle {
  margin-right: 3px;
  height: 5px;
  width: 5px;
  border-radius: 50%;
  display: inline-block;
}

.shapeContainer {
  padding-left: 10px;
  padding-right: 10px;
}

.prevGuessContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  padding-top: 10px;
}

.replayButton {
  margin: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 150px;
  height: 50px;
  background-color: #f1c2c2;
  border: 1px solid black;
  transition: all .1s ease-in-out;
}

.replayButton:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.selectionCircle {
  height: 50px;
  width: 50px;
  border-radius: 50%;
  display: inline-block;
  transition: all .1s ease-in-out;
}

.selectionCircle:hover {
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
}

.colorContainer {
  padding-left: 10px;
  padding-right: 10px;
}

.selectionPanelContainer {
  text-align: left;
  position: absolute;
  bottom: 10%;
}

